/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Link } from 'gatsby';

interface Props {
  value: string | null;
  totalCount: number;
}

const BlogCategory = ({ value, totalCount }: Props) => {
  return (
    <Link to={`/blog/#${value}`}>
      <div>
        <span>{value ? value : '미분류'}</span>
        <span css={{ marginLeft: 10 }}>{`(${totalCount})`}</span>
      </div>
    </Link>
  );
};

export default BlogCategory;
